module.exports = "import React from 'react';\nimport DayPicker from 'react-day-picker';\nimport 'react-day-picker/lib/style.css';\n\nconst WEEKDAYS_LONG = {\n  en: [\n    'Sunday',\n    'Monday',\n    'Tuesday',\n    'Wednesday',\n    'Thursday',\n    'Friday',\n    'Saturday',\n  ],\n  it: [\n    'Domenica',\n    'Lunedì',\n    'Martedì',\n    'Mercoledì',\n    'Giovedì',\n    'Venerdì',\n    'Sabato',\n  ],\n};\nconst WEEKDAYS_SHORT = {\n  en: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],\n  it: ['D', 'L', 'M', 'M', 'G', 'V', 'S'],\n};\nconst MONTHS = {\n  en: [\n    'January',\n    'February',\n    'March',\n    'April',\n    'May',\n    'June',\n    'July',\n    'August',\n    'September',\n    'October',\n    'November',\n    'December',\n  ],\n  it: [\n    'Gennaio',\n    'Febbraio',\n    'Marzo',\n    'Aprile',\n    'Maggio',\n    'Giugno',\n    'Luglio',\n    'Agosto',\n    'Settembre',\n    'Ottobre',\n    'Novembre',\n    'Dicembre',\n  ],\n};\n\nconst FIRST_DAY = {\n  en: 0,\n  it: 1, // Use Monday as first day of the week\n};\n\nfunction formatDay(d, locale = 'en') {\n  return `${WEEKDAYS_LONG[locale][d.getDay()]}, ${d.getDate()} ${\n    MONTHS[locale][d.getMonth()]\n  } ${d.getFullYear()}`;\n}\n\nfunction formatMonthTitle(d, locale = 'en') {\n  return `${MONTHS[locale][d.getMonth()]} ${d.getFullYear()}`;\n}\n\nfunction formatWeekdayShort(i, locale) {\n  return WEEKDAYS_SHORT[locale][i];\n}\n\nfunction formatWeekdayLong(i, locale) {\n  return WEEKDAYS_SHORT[locale][i];\n}\n\nfunction getFirstDayOfWeek(locale) {\n  return FIRST_DAY[locale];\n}\n\nconst localeUtils = {\n  formatDay,\n  formatMonthTitle,\n  formatWeekdayShort,\n  formatWeekdayLong,\n  getFirstDayOfWeek,\n};\n\nexport default function MyComponent() {\n  return <DayPicker locale=\"it\" localeUtils={localeUtils} />;\n}\n"


